
import {
  defineComponent,
  computed,  
} from "vue";

import * as Connection from "../../src/connection";

import { dropdownMidiEventType } from "../../src/UserDataConfig/Easyconfig";
import { PresetMidiControl } from "../../src/UserDataConfig/MidiRoutePreset/MidiRoutePreset";

import Card from "../a/card-component.vue";
import CardHeader from "../a/card-header.vue";
import CardBody from "../a/card-body.vue";
import ServerInOutPortsSelect from "../a/serverIn-out-ports-select.vue";
import SelectedMidiEventType from "../a/selected-midi-event-type.vue";
import SelectedMidiChannel from "../a/selected-midi-channel.vue";
import SelectedMidiData from "../a/selected-midi-data.vue";


export default defineComponent({
  components: {
    Card,
    CardHeader,
    CardBody,
    ServerInOutPortsSelect,
    SelectedMidiEventType,
    SelectedMidiChannel,
    SelectedMidiData,
  },
  emits: ['change'],
  props: {
    description: {
      type: String,
      required: true,
    },
    presetMidiControl: {
      type: PresetMidiControl,
      required: true,
    },    
  },
  setup(props) {
    const localPresetMidiControl = computed(  ()=> {
      const c =  Object.assign({},props.presetMidiControl);
      c.port =   props.presetMidiControl.port;     
      return c;         

        })
    


    const inPorts = computed(() => {
      return Connection.loginStatus.inPorts;
    });

    const channels = computed(() => {
      const ret = [];
      for (let i = 1; i <= 16; i++) {
        ret.push(i);
      }
      return ret;
    });

    const range127 = computed(() => {
      const ret = [];
      for (let i = 0; i <= 127; i++) {
        ret.push(i);
      }
      return ret;
    });

    return { dropdownMidiEventType, range127, channels, inPorts, localPresetMidiControl };
  },
});


